import {React, useState, useEffect} from "react";
import {useDispatch, useSelector} from "react-redux";
import CapitalItems from "../CapitalItems/CapitalItems";

import SwingButton from "../SwingButton/SwingButton";

import './CapitalList.css';
import {fetchData, setActiveId} from "../../store/reducers/RocetSlice";
import DigitalRubleEdit from "../Popup/DigitalRuble/DigitalRubleEdit/DigitalRubleEdit";
import TransportEdit from "../Popup/TransportForm/TransportEdit/TransportEdit";
import DepositEdit from "../Popup/DepositForm/DepositEdit/DepositEdit";
import ApartmentEdit from "../Popup/Realty/RealtyEdit/ApartmentEdit";
import CommercialEdit from "../Popup/Realty/RealtyEdit/CommercialEdit";
import HouseEdit from "../Popup/Realty/RealtyEdit/HouseEdit";
import LandPlotEdit from "../Popup/Realty/RealtyEdit/LandPlotEdit";

function CapitalList({handlerOpenChange}) {

  const allActives = useSelector((state) => state.rocketSlice.allActives)
  const realty = useSelector((state) => state.rocketSlice.realty)
  const deposits = useSelector((state) => state.rocketSlice.deposits)
  const transport = useSelector((state) => state.rocketSlice.transport)
  const digital_rubles = useSelector((state) => state.rocketSlice.digital_rubles)
  const markets = useSelector((state) => state.rocketSlice.markets)

  const [activButton, setActivButton] = useState('all-assets')

  function changeActivButton(e) {
    setActivButton(e.target.name)
  }

    return (
      <> 
          <div className="all-assets__info">
            <h3 className="all-assets__title">&nbsp;</h3>
            <p className="paragraph paragraph_all-assets">    ,    ,&nbsp;   </p>
          </div>
          <div className="card-list">
            <div className="card" id='card1'> 
              <p className="card-title"><span className="card-icon realty"></span>{` [${realty.quantity}]`}</p>
              <p className="card-sum">{`${realty.current_price || 0} `}</p>
            </div>

            <div className="card" id='card2'>
              <p className="card-title"><span className="card-icon bitcoin"></span>{` [-]`}</p>
              <p className="card-sum">- </p>
            </div>
            
            <div className="card" id='card3'>
              <p className="card-title"><span className="card-icon deposit"></span>{` [${deposits.quantity}]`}</p>
              <p className="card-sum">{`${deposits.current_price || 0} `}</p>
            </div>

            <div className="card" id='card4'>
              <p className="card-title"><span className="card-icon car"></span>{` [${transport.quantity}]`}</p>
              <p className="card-sum">{`${transport.current_price || 0} `}</p>
            </div>

            <div className="card" id='card5'>
              <p className="card-title"><span className="card-icon digital-ruble"></span>{`  [${digital_rubles.quantity}]`}</p>
              <p className="card-sum">{`${digital_rubles.current_price || 0} `}</p>
            </div>

            <div className="card" id='card6'>
              <p className="card-title"><span className="card-icon broker-account"></span>{`  [${markets.quantity}]`}</p>
              <p className="card-sum">{`${markets.current_price || 0} `}</p>
            </div>
          </div>

<SwingButton 
activButton={activButton}
changeActivButton={changeActivButton}
items={[
  {name: "all-assets", description: " "},
  {name: "realty", description: ""},
  {name: "transport", description: ""},
  {name: "deposit", description: ""},
  {name: "broker-account", description: " "},
  {name: "bitcoin", description: ""},
  {name: "digital-ruble", description: " "},
]}
/>
          <div className="capital__nav">
            <button className="button nav__button" id='activ'></button>
  
            <div className="nav-wrapper">
              <button className="button nav__button">.</button>
              <button className="button nav__button">.</button>
              <button className="button nav__button">, &#8381;</button>
              <button className="button nav__button">, %</button>
            </div>
          </div>
          
          <CapitalItems activButton={activButton} />


  {allActives.total_quantity === 0
? (<div className="capital-list__info">
    <p className="paragraph paragraph_items">  ,    </p>
    <button className="sota-button sota-button_capital" onClick= {handlerOpenChange}></button>
    </div>)
: <button className="sota-button sota-button_capital sota-button_roket" onClick= {handlerOpenChange}></button>
}
</>
    )
}

export default CapitalList;