import {React, useEffect} from "react";
import { useNavigate, Link } from "react-router-dom";

import logoImage from '../../../images/lending/logo-image.svg'


function FooterLend() {

  useEffect(() => {
    goTop()
  }, [])

  function goTop() {
    window.scrollTo(0, 0)
  }

  return (
    <footer className="footer">
      <div className="footer__menu">
      <Link to='/' on onClick={goTop} className="logo logo_footer">
        <img className="logo__image" alt="   " src={logoImage} />
        <p className="logo__text">Capital Control</p>
      </Link>
        <p className="footer__title about-title"> </p>
        <ul className=" footer-menu footer__about">
          <li className="footer-menu__paragraph"><a className="footer-link" href="#about-project"> </a></li>
          <li className="footer-menu__paragraph paragraph"><a className="footer-link" href="#advantages"></a></li>
          <li className="footer-menu__paragraph paragraph"><a className="footer-link" href="#rates"></a></li>
          <li className="footer-menu__paragraph paragraph"><a className="footer-link" href="#"></a></li>
        </ul>

        <p className="footer__title docs-title"></p>
        <ul className="footer-menu footer__docs">
          <li className="footer-menu__paragraph paragraph"><Link className="footer-link" to='/license-agreement'> </Link></li>
          <li className="footer-menu__paragraph paragraph"><Link className="footer-link" to="/privacy-policy"> </Link></li>
          <li className="footer-menu__paragraph paragraph"><a className="footer-link" href="#">   </a></li>
          <li className="footer-menu__paragraph paragraph"><a className="footer-link" href="#"> 5038179405</a></li>
          <li className="footer-menu__paragraph paragraph"><a className="footer-link" href="#"> 1235000073486</a></li>
        </ul>
      </div>

      <div className="footer__cookie-info">
        <p className="paragraph footer__paragraph">   cookie,    . ookie  ,     -. <Link to='/' className="link footer__link "> </Link></p>
        <button className="button white-button landing__white-button footer__button"></button>
      </div>
    </footer>
  )
}

export default FooterLend;