import React from "react";

function FinamInstruction({type}) {
  return (
    type ==='finam'
    ? (
      <div className='stock__info'>
        <p className='stock__info__title'> :</p>
        <ol className='popup__instruction'>
          <li className='stock__info__paragraph'>  <a href="#" className="link"></a>.</li>
          <li className='stock__info__paragraph'>  <a href="#" className="link">Comon</a>.</li>
          <li className='stock__info__paragraph'>  Comon  <a href="#" className="link"></a>.</li>
        </ol>

        <p className='stock__info__title'>  :</p>
        <ol className='popup__instruction'>
          <li className='stock__info__paragraph'>  +  .</li>
          <li className='stock__info__paragraph'> .   ,  .</li>
          <li className='stock__info__paragraph'> ,  ,    ( ,      )  +  .</li>
          <li className='stock__info__paragraph'> .</li>
          <li className='stock__info__paragraph'>   .</li>
        </ol>
        <p className='stock__info__paragraph'>      <a href="#" className="link"></a></p>
      </div>
    )
    : <></>
  )
}

export default FinamInstruction;