import React from "react";

function BinansInstruction({type}) {
  return (
    type ==='market'
    ? (
      <div className='stock__info'>
    <p className='stock__info__title'> API :</p>
    <ol className='popup__instruction'>
      <li className='stock__info__paragraph'>  <a href="#" className="link"></a></li>
      <li className='stock__info__paragraph'>  API,     .</li>
      <li className='stock__info__paragraph'>  ,  ,  .</li>
      <li className='stock__info__paragraph'>  API  ,    .     ,   </li>
    </ol>
    <p className='stock__info__paragraph'> <a href="#" className="link"></a>,    IP 31.129.102.22    .</p>
  
    </div>
    )
    : <></>
  )
}

export default BinansInstruction;