import React from "react";

function TinkoffInstruction({type}) {
return (
  type ==='tinkoff'
  ? (
  <div className='stock__info'>
    <p className='stock__info__title'>  :</p>
    <ol className='popup__instruction'>
      <li className='stock__info__paragraph'>     <a href="#" className="link"></a>.</li>
      <li className='stock__info__paragraph'> , .</li>
      <li className='stock__info__paragraph'>      .</li>
      <li className='stock__info__paragraph'>  TINKOFF INVEST API  (,  Readonly token). ,    . ,     .</li>
      <li className='stock__info__paragraph'>  .     ,   .      .</li>
    </ol>
    <p className='paragraph'>      <a href="#" className="link"></a></p>
   </div>
)
: <></>
)
}

export default TinkoffInstruction;